import * as React from 'react'
import { styled } from '@mui/material/styles'
import Tabs from '@mui/material/Tabs'
import Tab from '@mui/material/Tab'
import { withRouter } from 'react-router'

export interface StyledTabProps {
  label: string
  style?: any
}

export interface StyledTabsProps {
  children?: React.ReactNode
  value: number
  onChange: (event: React.SyntheticEvent, newValue: number) => void
  style?: any
}

export const AntTabs = styled((props: StyledTabsProps) => (
  <Tabs {...props} TabIndicatorProps={{ children: <span className="MuiTabs-indicatorSpan" /> }} />
))({
  margin: '40px auto 20px',
  '& .MuiTabs-indicator': {
    display: 'flex',
    justifyContent: 'center',
    height: 4,
    backgroundColor: 'transparent',
  },
  '& .MuiTabs-indicatorSpan': {
    maxWidth: 20,
    width: '100%',
    borderRadius: 5,
    background: 'linear-gradient(270deg, #7AE9F0 0%, #61C3F7 100%);',
  },
})

export const AntTab = styled((props: StyledTabProps) => <Tab disableRipple {...props} />)(({ theme }) => ({
  textTransform: 'none',
  minWidth: 0,
  [theme.breakpoints.up('sm')]: {
    minWidth: 0,
  },
  fontFamily: 'Heebo Medium',
  fontWeight: 500,
  marginRight: theme.spacing(1),
  color: 'rgba(0, 0, 0, 0.5)',
  fontSize: '22px',
  '&:hover': {
    color: '#000000',
    opacity: 1,
  },
  '&.Mui-selected': {
    color: '#000000',
    fontWeight: theme.typography.fontWeightMedium,
  },
  '&.Mui-focusVisible': {
    backgroundColor: '#d1eaff',
  },
}))

function CustomizedTabs(props) {
  const { tab, tabsStyle, tabStyle, defaultTabsValue, history } = props
  const [value, setValue] = React.useState(Number(defaultTabsValue) || 0)

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue)

    if (defaultTabsValue !== undefined) {
      history.push(`/history/claimed/${newValue}`)
    }
  }

  return (
    <>
      <AntTabs style={tabsStyle} value={value} onChange={handleChange}>
        {tab.map((it) => (
          <AntTab style={tabStyle} key={it.label} label={it.label} />
        ))}
      </AntTabs>
      {tab.map((item, index) => index === value && <React.Fragment key={item.label}>{item.component}</React.Fragment>)}
    </>
  )
}
export default withRouter(CustomizedTabs)
