import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { Grid, Divider } from '@mui/material'
import { styled } from '@mui/material/styles'
import { BTitle, Title } from 'components/CommonComponent'
import { useWeb3React } from 'web3-react-core'
import Tabs from 'components/Tabs'
import Card from 'components/Card'
import { Button, useWalletModal } from 'components/uikit'
// import { BigNumber } from '@ethersproject/bignumber'
import contracts from 'config/constants/contracts'
import { formatNumberWithUnits } from 'utils/formatTheValue'
import { getContract, getProviderAndSigner, getSigner, isSupportedChainId } from 'utils/web3React'
import useToast from 'hooks/useToast'
import useHashPowerToken from 'hooks/useHashPowerToken'
import MinerABI from 'config/abi/Miner.json'
import { TokenUtils, toLargeUnits, toSmallUnits } from 'utils/transformHelper'
import useAuth from 'hooks/useAuth'
import BlockUpdaterABI from 'config/abi/BlockUpdater.json'
import { chainConfigs, SupportedChainId, valueToKeyMap } from 'config/constants/tokenConfig'
import { mintTokenChainConfigs, MintTokenKey } from 'config/constants/mintToken'
import { ethers } from 'ethers'
import { INFURA_NETWORK_URLS } from 'config/constants/chains'
import { usePositionInfo } from 'hooks/news/usePoistion'
import Multicall from 'utils/multicall'
import { TransactionSubmittedInfo } from 'components/TransactionInfo'
import { ToastTitle } from 'config/constants/contants'
import HoldingsWallet from './components/HoldingsWallet'
import OutstandingDebt from './components/OutstandingDebt'
import tokens from '../../config/constants/tokens'
import useSystemParams from '../../hooks/useSystemParams'
import useEpoch from '../../hooks/useEpoch'

const Wrapper = styled('div')`
  width: 100%;
  height: 100%;
  overflow: scroll;
`
const Box = styled('div')`
  box-sizing: border-box;
  margin-top: 26px;
  padding-right: 44px;
  min-width: 1024px;
  padding-bottom: 100px;

  &::-webkit-scrollbar {
    display: none;
  }
`

const CommonCard = styled(Card)`
  padding: 30px;
  height: 100%;

  &.rightPanel {
    h4 {
      line-height: 20px;
    }
  }
  h4 {
    font-family: PingFangSC-Medium, sans-serif;
    font-weight: 480;
    font-size: 16px;
    line-height: 22px;
    color: black;
  }

  p {
    font-family: PingFangSC-Semibold, sans-serif;
    font-size: 40px;
    font-weight: 550;
    line-height: 1;
    color: #6ed6f3;
    margin-top: 12px;
    margin-bottom: 8px;
    letter-spacing: 1px;
    sub {
      font-size: 16px;
    }
  }
  span {
    font-family: PingFangSC-Regular, sans-serif;
    font-size: 14px;
  }
`
const SubTitle = styled(Title)`
  margin-bottom: 14px;
  font-family: PingFangSC-Semibold, sans-serif;
  font-weight: 550;
  font-size: 22px;
  line-height: 31px;
  color: #000000;
`

const ClaimButton = styled(Button)`
  background: linear-gradient(90deg, #61c2f7 0%, #7ae9f0 100%) !important;
  border-radius: 20px !important;
  box-shadow: none !important;
  height: 30px !important;
  line-height: 30px !important;
  color: #000 !important;
  font-size: 14px !important;
  display: block !important;

  &.disabled {
    background: #ddd !important;
    color: #eee !important;
  }
`

// const TableCard = styled(Card)`
//   margin-bottom: 20px;
// `

const { claimHelper, uniswapV2Multicall } = contracts
const minerInterface = new ethers.utils.Interface(MinerABI)
const blockUpdaterInterface = new ethers.utils.Interface(BlockUpdaterABI)
function MyPage() {
  const { prevEpoch } = useEpoch()
  const [claimStatus, setClaimStatus] = useState(false)
  const { hashPowerTokenList, walletTokenBalance, setRefresh } = useHashPowerToken()
  const { signer } = useSystemParams()
  const { chainId, account, library, active } = useWeb3React()
  const { toastError, toastSuccess, toastInfo } = useToast()
  const [walletTotal, setWalletTotal] = useState(0)
  const [avatarTotal, setAvatarTotal] = useState(0)
  const [dailyInterestRewards, setDailyInterestRewards] = useState(0)
  const [dailyObligation, setDailyObligation] = useState(0)
  const [rewardsData, setRewardsData] = useState(null)
  const [allRewards, setAllRewards] = useState([])
  const { login, logout } = useAuth()
  const { onPresentConnectModal } = useWalletModal(login, logout, account)

  const isLogin = useMemo(() => {
    return active && chainId && isSupportedChainId(chainId)
  }, [active, chainId])
  const { positionAll } = usePositionInfo()

  const claimAll = useCallback(async () => {
    if (!account) {
      onPresentConnectModal()
      return
    }

    if (signer) {
      const claimContract = getContract(signer, claimHelper.abi, claimHelper.address[chainId])
      if (claimContract) {
        try {
          const params = []
          for (const item of hashPowerTokenList) {
            params.push([item.address[chainId], account])
            if (item.avatar && Number(item.avatar) > 0) {
              params.push([item.address[chainId], item.avatar])
            }
          }
          setClaimStatus(true)
          const txInfo = await claimContract.multipleClaim(params)
          if (txInfo) {
            toastInfo(ToastTitle.submited, <TransactionSubmittedInfo txHash={txInfo.hash} content='Waiting for confirmation'/>)
            await txInfo.wait()
            toastSuccess(ToastTitle.success, <TransactionSubmittedInfo txHash={txInfo.hash} content='Transaction has been confirmed'/>)
            setRefresh((state) => !state)
            setClaimStatus(false)
          }
        } catch (e: any) {
          setClaimStatus(false)
          toastError(ToastTitle.failed, e?.reason)
        }
      }
    }
  }, [
    account,
    chainId,
    hashPowerTokenList,
    setRefresh,
    signer,
    toastError,
    toastInfo,
    toastSuccess,
    onPresentConnectModal,
  ])
  const pendingReward = useCallback(
    async (abi, updaterAddress, token, address, decimals, balance, lastModifiedEpoch?) => {
      if (library && chainId) {
        const _signer = getSigner(library, address)
        const updater = getContract(_signer, abi, updaterAddress)
        const miner = getContract(_signer, MinerABI, tokens[token].address[chainId])

        try {
          if (updater && walletTokenBalance && miner) {
            const holderInfos = await miner.holderInfoMapping(address)
            console.log('holderInfos', holderInfos)
            if (lastModifiedEpoch) {
              return updater.pendingReward(balance, holderInfos[0], holderInfos[1], holderInfos[2])
            }
            return updater.pendingReward(
              (toSmallUnits('1', decimals) as any) * balance,
              holderInfos[0],
              holderInfos[1],
              holderInfos[2],
            )
          }
        } catch (err) {
          console.log('>err', abi, updaterAddress, token, address, decimals, balance, lastModifiedEpoch, err)
        }
      }

      return 0
    },
    [chainId, library, walletTokenBalance],
  )

  const _pendingReward = useCallback(async () => {
    if (isLogin && walletTokenBalance) {
      const _tokens = mintTokenChainConfigs[chainId as SupportedChainId].tokens
      const { provider } = getProviderAndSigner(INFURA_NETWORK_URLS[chainId as SupportedChainId], account, chainId)
      const multicall = new Multicall(chainId, uniswapV2Multicall.address, uniswapV2Multicall.abi)

      for (const key in _tokens) {
        if (Object.prototype.hasOwnProperty.call(_tokens, key)) {

          const tokenInfo = _tokens[key as MintTokenKey]
          const positionInfo = positionAll[key as MintTokenKey]
          if (!walletTokenBalance[tokenInfo.symbol] || !positionInfo.isEnable || (positionInfo.isEnable && positionInfo.balance.isZero())) {
            continue
          }


          const holderInfoMappingCalls = [
            {
              target: tokenInfo.address,
              callData: minerInterface.encodeFunctionData('holderInfoMapping', [account]),
              functionSignature: 'holderInfoMapping',
            },
            {
              target: tokenInfo.address,
              callData: minerInterface.encodeFunctionData('holderInfoMapping', [positionInfo.positionAddress]),
              functionSignature: 'holderInfoMapping',
            },
          ]
          multicall.addAbi(tokenInfo.address, MinerABI)
          multicall.addAbi(tokenInfo.blockUpdater, BlockUpdaterABI)
          const {
            returnData: { holderInfoMapping },
          } = await multicall.aggregate(holderInfoMappingCalls)
          // updater.pendingReward(
          //   (toSmallUnits('1', decimals) as any) * balance,
          //   holderInfos[0],
          //   holderInfos[1],
          //   holderInfos[2],
          // )
          // console.log('first', toSmallUnits(String(walletTokenBalance[tokenInfo.symbol]), tokenInfo.decimals))

          // const pendingRewardCalls = holderInfoMapping.map(([holder], index)=> {
          //   console.log('pendingRewardCalls', [
          //     index === 0 ? toSmallUnits(walletTokenBalance[valueToKeyMap[key]], tokenInfo.decimals) : positionInfo.balance,
          //     holder[1],
          //     holder[2],
          //     holder[3],
          //   ])
          //   return {
          //     target: tokenInfo.blockUpdater,
          //     callData: blockUpdaterInterface.encodeFunctionData('pendingReward', [
          //       index === 0 ? toSmallUnits(String(walletTokenBalance[tokenInfo.symbol]), tokenInfo.decimals) : positionInfo.balance,
          //       holder[1],
          //       holder[2],
          //       holder[3],
          //     ]),
          //   }
          // })
        }
      }
    }
  }, [account, chainId, isLogin, positionAll, walletTokenBalance])

  const awaitRewardsData = useCallback(async () => {
    const data = {}
    for (const item of hashPowerTokenList) {
      const [avatarRewards, walletRewards] = await Promise.all([
        pendingReward(
          item.blockUpdater.abi,
          item.blockUpdater.address[chainId],
          item.token,
          item.position,
          item.decimals,
          (item.avatarAssetList && item.avatarAssetList[item.token]) || 0,
        ),
        pendingReward(
          item.blockUpdater.abi,
          item.blockUpdater.address[chainId],
          item.token,
          account,
          item.decimals,
          walletTokenBalance[item.token] || 0,
        ),
      ])
      data[item.token] = {
        walletRewards,
        avatarRewards,
        walletEstimateDailyReward: item.rewards * walletTokenBalance[item.token],
        avatarEstimateDailyReward: item.avatarAssetList ? item.rewards * item.avatarAssetList[item.token] : 0,
      }
    }

    setRewardsData(data)
  }, [account, chainId, hashPowerTokenList, pendingReward, walletTokenBalance])
  useEffect(() => {
    if (hashPowerTokenList && hashPowerTokenList.length && walletTokenBalance) {
      awaitRewardsData()
    }
  }, [awaitRewardsData, hashPowerTokenList, walletTokenBalance])

  useEffect(() => {
    _pendingReward()
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isLogin, walletTokenBalance])

  useEffect(() => {
    if (hashPowerTokenList && walletTokenBalance && rewardsData) {
      let _walletTotal = 0
      let _avatarTotal = 0
      let _dailyInterestRewards = 0
      let _dailyObligation = 0
      const _rewardsAll = []
      for (const item of hashPowerTokenList) {
        if (walletTokenBalance[item.token]) {
          _walletTotal +=
            walletTokenBalance[item.token] *
            Number(toLargeUnits(item.tokensValues[item.token], tokens.WBTC.decimals)) *
            item.WBTCRouter.USDT
        }
        if (item.avatarAssetList) {
          _avatarTotal +=
            item.avatarAssetList[item.token] *
            Number(toLargeUnits(item.tokensValues[item.token], tokens.WBTC.decimals)) *
            item.WBTCRouter.USDT
        }
        if (rewardsData[item.token]) {
          console.log('rewardsData[item.token].walletEstimateDailyReward :>> ', item.balance)
          const walletEstimateDailyReward = rewardsData[item.token].walletEstimateDailyReward / 10 ** 8 // TODO resolve overflow
          const avatarEstimateDailyReward = rewardsData[item.token].avatarEstimateDailyReward / 10 ** 8 // TODO resolve overflow
          _dailyInterestRewards += (walletEstimateDailyReward + avatarEstimateDailyReward) * item.WBTCRouter.USDT
          _dailyObligation += (((item.rewards / 10 ** 8) * item.balance) / 10 ** 6) * item.WBTCRouter.USDT

          const walletRewards = Number(
            toLargeUnits(rewardsData[item.token].walletRewards.epochReward, tokens.WBTC.decimals),
          )
          const avatarRewards = Number(
            toLargeUnits(rewardsData[item.token].avatarRewards.epochReward, tokens.WBTC.decimals),
          )
          const lpAllRewards = walletRewards + avatarRewards
          const rewardsTransfromToUsdt = (walletRewards + avatarRewards) * item.WBTCRouter.USDT
          _rewardsAll.push({
            rewards: lpAllRewards,
            usdt: rewardsTransfromToUsdt,
            lptoken: item.token,
          })
        }

        setWalletTotal(_walletTotal)
        setAvatarTotal(_avatarTotal)
        setDailyInterestRewards(_dailyInterestRewards)
        setDailyObligation(_dailyObligation)
        setAllRewards(_rewardsAll)
      }
    }
  }, [hashPowerTokenList, rewardsData, walletTokenBalance])

  const totalRwardsWbtc = useMemo(() => {
    const total = allRewards.reduce((acc, reward) => acc + reward.rewards, 0)
    // return TokenUtils.formatTokenAmount(total);
    return total
  }, [allRewards])
  const totalRwardsUsdt = useMemo(() => {
    const totalUSDT = allRewards.reduce((acc, reward) => acc + reward.usdt, 0)
    return TokenUtils.formatCurrencyTwoDecimals(totalUSDT)
  }, [allRewards])

  const rewardPannel = useMemo(() => {
    return (
      <Grid container style={{ marginBottom: 18, lineHeight: '20px', fontSize: '14px' }}>
        <Grid item xs={6}>
          <span>{(totalRwardsWbtc && totalRwardsWbtc.toFixed(8)) || totalRwardsWbtc} WBTC</span>
        </Grid>
        <Grid item xs={6} dir="rtl">
          <span>${totalRwardsUsdt}</span>
        </Grid>
      </Grid>
    )
  }, [totalRwardsUsdt, totalRwardsWbtc])
  return (
    <Wrapper>
      <BTitle>My Page</BTitle>
      <Box>
        <Grid container spacing={2}>
          <Grid item xs={8}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <CommonCard>
                  <SubTitle>Holdings - Synthetic Hashpower Token</SubTitle>
                  <Grid container>
                    <Grid item xs={6}>
                      <h4>Hashpower Token in Wallet </h4>
                      <p>
                        <sub></sub>${TokenUtils.formatNumberWithUnits(walletTotal)}
                      </p>
                    </Grid>
                    <Grid item xs={6}>
                      <h4>Hashpower Token in Collateral</h4>
                      <p>
                        <sub></sub>${TokenUtils.formatNumberWithUnits(avatarTotal)}
                      </p>
                    </Grid>
                  </Grid>
                </CommonCard>
              </Grid>
              <Grid item xs={12} style={{ paddingTop: 20 }}>
                <CommonCard style={{ paddingBottom: 33 }}>
                  <SubTitle>Claimable Rewards</SubTitle>
                  <Grid container spacing={2}>
                    <Grid item xs={6}>
                      {rewardPannel}
                      <Divider style={{ marginBottom: 18 }} />
                      <Grid container style={{ marginBottom: 20, lineHeight: '20px', fontSize: '14px' }}>
                        <Grid item xs={6}>
                          <span>Total</span>
                        </Grid>
                        <Grid item xs={6} dir="rtl">
                          <span>${totalRwardsUsdt}</span>
                        </Grid>
                      </Grid>
                    </Grid>
                    <Grid item xs={6} style={{ paddingTop: 0, paddingLeft: 97 }}>
                      <img
                        src="/images/eth.png"
                        style={{ width: 108, height: 108, margin: '5px 0 0 35px', display: 'block' }}
                        alt="illustration"
                      />
                      <ClaimButton
                        disabled={claimStatus}
                        style={{ margin: '30px 0 0' }}
                        className={`${claimStatus ? 'disabled' : ''}`}
                        onClick={claimAll}
                      >
                        Claim All Rewards
                      </ClaimButton>
                    </Grid>
                  </Grid>
                </CommonCard>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={4}>
            <CommonCard className="rightPanel">
              <SubTitle>Epoch {prevEpoch.epoch} Balance</SubTitle>
              <Grid container>
                <Grid item xs={12} style={{ marginBottom: 30 }}>
                  <h4>Rewards Earned</h4>
                  <p>
                    <sub></sub>${formatNumberWithUnits(dailyInterestRewards)}
                  </p>
                </Grid>
                <Grid item xs={12} style={{ marginBottom: 30 }}>
                  <h4>Interest Paid</h4>
                  <p>
                    <sub></sub>${formatNumberWithUnits(dailyObligation)}
                  </p>
                </Grid>
                <Grid item xs={12} style={{ marginBottom: 30 }}>
                  <Divider />
                </Grid>

                <Grid item xs={12} style={{ marginBottom: 36 }}>
                  <h4>Net Rewards</h4>
                  <p>
                    <sub></sub>
                    {formatNumberWithUnits(dailyInterestRewards - dailyObligation).includes('-')
                      ? formatNumberWithUnits(dailyInterestRewards - dailyObligation).replace('-', '-$')
                      : `$${formatNumberWithUnits(dailyInterestRewards - dailyObligation)}`}
                  </p>
                </Grid>
              </Grid>
            </CommonCard>
          </Grid>
        </Grid>
        <div style={{ width: '100%' }}>
          <Tabs
            tabsStyle={{
              minHeight: 36,
              paddingLeft: 21,
            }}
            tabStyle={{
              paddingTop: 0,
              paddingBottom: 0,
              minHeight: 32,
              lineHeight: '32px',
            }}
            tab={[
              {
                label: 'All',
                component: (
                  <>
                    <HoldingsWallet rewardsData={rewardsData} />
                    {/* <HoldingsAvatar rewardsData={rewardsData} />    / */}
                    <OutstandingDebt />
                  </>
                ),
              },
              {
                label: 'Holdings',
                component: <HoldingsWallet rewardsData={rewardsData} />,
              },
              // {
              //   label: 'Holdings - Avatar',
              //   component: <HoldingsAvatar rewardsData={rewardsData} />
              // },
              {
                label: 'Outstanding Debt',
                component: <OutstandingDebt />,
              },
            ]}
          />
        </div>
      </Box>
    </Wrapper>
  )
}

export default React.memo(MyPage)
