import React from 'react'
import { Link } from 'react-router-dom'
import Card from 'components/Card'
import { useWeb3React } from 'web3-react-core'
import Table, { StyledTableCell } from 'components/Table'
import { formatNumber, formatWBTC } from 'utils/formatTheValue'
import { TokenUtils, toLargeUnits } from 'utils/transformHelper'
import Action from './Action'
import Title from './Title'
import HistoryIcon from './icon/History'
import tokens, { TokensIcon } from '../../../config/constants/tokens'
import useHashPowerToken from '../../../hooks/useHashPowerToken'

const columns = [
  {
    id: 'ticker',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Ticker',
  },
  {
    id: 'markPrice',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Mark Price',
  },
  {
    id: 'HoldingsWallet',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'In Wallet',
  },
  {
    id: 'HoldingsAvatar',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'In Collateral',
  },
  {
    id: 'claimableWallet',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Claimable - In Wallet',
  },
  {
    id: 'claimableAvatar',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Claimable - In Collateral',
  },
  {
    id: 'action',
    numeric: false,
    disablePadding: false,
    isSort: false,
    label: 'Action',
  },
]
/*
 * will
 * add filter
 *
 *
 * */
function HoldingsWallet({ rewardsData }) {
  const { hashPowerTokenList, walletTokenBalance } = useHashPowerToken()
  const { account } = useWeb3React()
  return (
    <Card style={{ marginBottom: 20 }}>
      <Title>
        <h3>Holdings - Hashpower Token</h3>
        <Link to="/history/claimed/0">
          <HistoryIcon /> Claimed History
        </Link>
      </Title>
      <Table
        columns={columns}
        dataSource={hashPowerTokenList || []}
        pagination={false}
        isMyPage
        content={(row, index, labelId) => (
          <>
            <StyledTableCell style={{ paddingLeft: 0 }} component="th" id={labelId} scope="row">
              <img
                src={TokensIcon[row.ticker]}
                style={{ display: 'block', marginRight: 7, float: 'left' }}
                alt="icon"
              />
              {row.ticker}
            </StyledTableCell>
            <StyledTableCell>{row.tokensValues ? TokenUtils.formatFourDecimals(formatWBTC(row.tokensValues[row.token])) : 0} WBTC</StyledTableCell>
            <StyledTableCell>{walletTokenBalance ? formatNumber(walletTokenBalance[row.token]) : 0}</StyledTableCell>
            <StyledTableCell>
              {Number(row.position) && row.avatarAssetList ? formatNumber(row.avatarAssetList[row.token]) : 0}
            </StyledTableCell>
            <StyledTableCell>
              {rewardsData && rewardsData[row.token] && rewardsData[row.token].walletRewards[0]
                ? toLargeUnits(rewardsData[row.token].walletRewards.epochReward, tokens.WBTC.decimals)
                : 0}{' '}
              WBTC
            </StyledTableCell>
            <StyledTableCell>
              {rewardsData && rewardsData[row.token] && rewardsData[row.token].avatarRewards[0]
                ? toLargeUnits(rewardsData[row.token].avatarRewards.epochReward, tokens.WBTC.decimals)
                : 0}{' '}
              WBTC
            </StyledTableCell>
            <StyledTableCell>
              <Action row={row} address={account} rewardsData={rewardsData && rewardsData[row.token]} />
            </StyledTableCell>
          </>
        )}
      />
    </Card>
  )
}
export default HoldingsWallet
