import React, { useCallback, useEffect, useState } from 'react'
import { Button, Stack } from '@mui/material'
import { styled } from '@mui/material/styles'
import uuid from 'uuid/dist/v4'
import { Button as ButtonStyled } from 'components/uikit/components/Button'
import { useWeb3React } from 'web3-react-core'
import tokens from 'config/constants/tokens'
import { toLargeUnits } from 'utils/transformHelper'
import { TransactionSubmittedInfo } from 'components/TransactionInfo'
import { ToastTitle } from 'config/constants/contants'
import addAsset from '../../../utils/addAsset'
import { getContract } from '../../../utils/web3React'
import useToast from '../../../hooks/useToast'
import useSystemParams from '../../../hooks/useSystemParams'
import useHashPowerToken from '../../../hooks/useHashPowerToken'
import contracts from '../../../config/constants/contracts'

const ActionButton = styled(Button)`
  text-transform: none;
  cursor: pointer;
  font-family: PingFangSC-Regular, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #2aa1ff;
  padding: 0;
`
export const AlertButton = styled(ButtonStyled)`
  box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.043);
  border-radius: 14px;
  background: linear-gradient(270deg, #7ae9f1 0%, #61c3f7 100%);
  border-radius: 14px;
  font-size: 14px;
  height: 32px;
  line-height: 32px;
  color: #000;
  font-weight: normal;
`

const { claimHelper } = contracts
function Action({ row, address, rewardsData }) {
  const { signer } = useSystemParams()
  const [claimStatus, setClaimStatus] = useState(false)
  const { setRefresh } = useHashPowerToken()
  const { connector, active, chainId, account } = useWeb3React()
  const { toastInfo, toastSuccess, toastError } = useToast()
  const { ticker } = row
  const { walletTokenBalance } = useHashPowerToken()
  const [isAdding, setIsAdding] = useState(false)

  const addToMetaMask = useCallback(async () => {
    if (active && connector && ticker) {
      setIsAdding(true)
      await addAsset({ connector, tokenSymbol: ticker, chainId })
      setIsAdding(false)
    }
  }, [connector, active, chainId, ticker])

  useEffect(() => {
    if (!rewardsData) {
      return
    }
    if (!rewardsData.walletRewards && !rewardsData.avatarRewards) {
      setClaimStatus(true)
      return
    }
    if (
      rewardsData.walletRewards.epochReward.toNumber() === 0 &&
      rewardsData.avatarRewards.epochReward.toNumber() === 0
    ) {
      setClaimStatus(true)
    }
  }, [rewardsData, row, walletTokenBalance])
  const claimAll = useCallback(async () => {
    if (signer) {
      const claimContract = getContract(signer, claimHelper.abi, claimHelper.address[chainId])
      if (claimContract) {
        try {
          const params = []
          params.push([row.address[chainId], account])

          if (row.avatar && Number(row.avatar) > 0) {
            params.push([row.address[chainId], row.avatar])
          }
          setClaimStatus(true)
          // }
          const txInfo = await claimContract.multipleClaim(params)
          const avatarRewards = toLargeUnits(rewardsData.avatarRewards[0], tokens.WBTC.decimals)
          const walletRewards = toLargeUnits(rewardsData.walletRewards[0], tokens.WBTC.decimals)
          if (txInfo) {
            let claimRewardsList = window.localStorage.getItem('claimRewardsList')
            if (claimRewardsList) {
              claimRewardsList = JSON.parse(claimRewardsList)
              const _data = [
                // @ts-ignore
                ...claimRewardsList,
                {
                  id: uuid(),
                  txHash: txInfo.hash,
                  date: new Date().getTime(),
                  tokenSymbol: row.token,
                  walletRewards,
                  avatarRewards,
                  type: 'My Wallet',
                },
              ]
              if (params.length > 1) {
                _data.push({
                  id: uuid(),
                  txHash: txInfo.hash,
                  date: new Date().getTime(),
                  tokenSymbol: row.token,
                  walletRewards,
                  avatarRewards,
                  type: 'Collateral',
                })
              }
              window.localStorage.setItem('claimRewardsList', JSON.stringify(_data))
            } else {
              const _data = [
                {
                  id: uuid(),
                  txHash: txInfo.hash,
                  date: new Date().getTime(),
                  tokenSymbol: row.token,
                  walletRewards,
                  avatarRewards,
                  type: 'My Wallet',
                },
              ]
              if (params.length > 1) {
                _data.push({
                  id: uuid(),
                  txHash: txInfo.hash,
                  date: new Date().getTime(),
                  tokenSymbol: row.token,
                  walletRewards,
                  avatarRewards,
                  type: 'Total Collateral',
                })
              }
              window.localStorage.setItem('claimRewardsList', JSON.stringify(_data))
            }

            toastInfo(ToastTitle.submited, <TransactionSubmittedInfo txHash={txInfo.hash} content='Waiting for confirmation'/>)
            const result = await txInfo.wait()
            toastSuccess(ToastTitle.success, <TransactionSubmittedInfo txHash={txInfo.hash} content='Transaction has been confirmed'/>)
            setRefresh((state) => !state)

            if (result) {
              setClaimStatus(false)
            }
          }
        } catch (e: any) {
          setClaimStatus(false)
          toastError(ToastTitle.failed, e?.reason)
        }
      }
    }
  }, [account, chainId, rewardsData, row, setRefresh, signer, toastError, toastInfo, toastSuccess])

  return (
    <div>
      <Stack direction="row" spacing={2}>
        <ActionButton disabled={claimStatus} onClick={claimAll}>
          Claim Rewards
        </ActionButton>
        {/* <ActionButton
          onClick={() => {
            window.open(
              `https://app.uniswap.org/#/swap?inputCurrency=${tokens.WBTC.address[chainId]}&outputCurrency=${row.address[chainId]}&chain=${SupportedChainId[chainId]}`,
            )
          }}
        >
          Uniswap Trade
        </ActionButton> */}
        <ActionButton disabled={isAdding} onClick={addToMetaMask}>
          Add to Metamask
        </ActionButton>
      </Stack>
    </div>
  )
}

export default Action
